import { useCallback } from "react"

const Popup = ({clickHandler , text , setShow}) => {
    const closeFunction = useCallback( () => {
      setShow((value) => ({...value , isActive : false}))
    } , [setShow] )
    return(
      <div className="wrapper__popup">
    <div className="black__wrapper" onClick={() => {
      closeFunction()
    }}>
      </div>
      <div className="popup">
        <p>{text}</p>
        <div className="popup__bottom">
          <button onClick={clickHandler} className="button">–î–∞</button>
          <button onClick={closeFunction} className="button">–ù–µ—Ç</button>
        </div>
      </div>
      </div>
    
    )
  }
  export default Popup